import { Component, OnInit, Input } from '@angular/core';
import { FormControl } from '@angular/forms';

import { DatePipe } from '@angular/common';
import { MatSnackBar, MatDialog } from '@angular/material';
import { UpholsteryService, ProjectService } from '@app/core';
import { Observable } from 'rxjs';
import {
    AddAcknowledgementComponent,
	AcknowledgementDialogComponent,
} from '../../../components';
import { Upholstery, BaseResource, Project } from '@app/core/models';
import { Quotation, Item } from '@app/core/models/quotation';
import { take } from 'rxjs/operators';


@Component({
  selector: 'quotation',
  templateUrl: './quotation.component.html',
  styleUrls: ['./quotation.component.less'],
  providers: [DatePipe, MatSnackBar, MatDialog]
})
export class QuotationComponent implements OnInit {
    products: Array<Item | Upholstery> = [];
    filteredProducts: Observable<BaseResource[]> | undefined;
    newItemAutoControl = new FormControl();
    @Input() quotation!: Quotation; // @ts-ignore
    public active: boolean = false;
    public panelExpanded = false;

    constructor(private snackBar: MatSnackBar,
                public upholsteryService: UpholsteryService,
                public upholstery: Upholstery,
                public project: Project,
                public projectService: ProjectService,
                public dialog: MatDialog) {
    }

    ngOnInit() {
    }

    public setProject(project: Project | string): void {
        const p =
            typeof project === 'string'
                ? {
                    id: null as null,
                    codename: project,
                }
                : project;

        this.quotation.project = p as Project ;
    }

    public showCreateAcknowledgement(event?: Event): void {
        
        if (event) {
            event.preventDefault();
            event.stopPropagation();
        }
        

        const dialogRef = this.dialog.open(AddAcknowledgementComponent, {
            width: '90vw',
            data: {quotation: this.quotation}
        });

        dialogRef.beforeClose().pipe(take(1)).subscribe((res) => {
            if (res) {
                this.quotation.acknowledgement = res;
                this.quotation.save();
            }
        });
    }

    public viewAcknowledgement(event: Event): void {
        if (event) {
            event.preventDefault();
            event.stopPropagation();
        }

        const dialogRef = this.dialog.open(AcknowledgementDialogComponent, {
            width: '90vw',
            data: {acknowledgement: this.quotation.acknowledgement}
        });

        dialogRef.beforeClose().pipe(take(1)).subscribe((res) => {
            if (res) {
                this.quotation.acknowledgement = res;
                this.quotation.save();
            }
        });

    }

    public update(event: Event): void {
        if (event) {
            event.preventDefault();
            event.stopPropagation();
        }
        
        if (this.quotation.id) {
            this.openSnackBar('Updating Quotation'.concat(': ', String(this.quotation.id)));

            this.quotation.save(() => {
                this.openSnackBar('Quotation '.concat(String(this.quotation.id), ' Updated'));
            });
        }
        
    }

    public cancel(event: Event): void {
        if (event) {
            event.preventDefault();
            event.stopPropagation();
        }

        this.quotation.status = 'cancelled';
        if (this.quotation.id) {
			this.openSnackBar('Cancelling Quotation'.concat(': ', String(this.quotation.id)));

			this.quotation.save(() => {
				this.openSnackBar('Quotation '.concat(String(this.quotation.id), ' Cancelled'));
			});
		}
    }

    public displayTitle(obj: BaseResource | undefined): string {
        return obj ? obj.description ? obj.description : ''  : '';
    }

    public openFile(data: {url: string}): void {
        window.open(data.url);
    }

    public updateItemImage(item: {image?: {}}, files: FileList) {
        this.uploadImage(files).subscribe((res) => {
            item.image = res;
            console.log(item);
            console.log(this.quotation.items);
            this.snackBar.open('Image Uploaded', 'Dimiss', {duration: 3000});
        });
    }

    public addItem(val: Item | string | Upholstery): void {
        let item: Item | Upholstery;

        if (typeof val === 'string') {
            item = new Item();
            item.description = val;
        } else {
            item = val;
        }
        
        this.quotation.items.push(item);
    }

    private openSnackBar(message: string, action: string = 'Dismiss'): void {
        const ref = this.snackBar.open(message, action, {
            duration: 2000,
        });

        ref.onAction().subscribe(() => ref.dismiss());
    }

    private uploadImage(files: FileList) {
        this.snackBar.open('Uploading Image...', 'Dismiss');
        const file = files[0];

        return this.quotation.upload(file, '/api/v1/acknowledgement/file/');
    }
}
