import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { CustomerService, UpholsteryService, ProjectService } from '@app/core';
import {
	Quotation,
	Customer,
	Upholstery,
	Project,
	Address,
} from '@app/core/models';
import { MatSnackBar, MatDialogRef } from '@angular/material';
import {
	FormBuilder,
	FormGroup,
	Validators,
	AbstractControl,
} from '@angular/forms';
import { AddOrderComponent } from '@app/shared/components';
import { ArrayValidators } from '@app/shared/validators';

@Component({
	templateUrl: './add-quotation.component.html',
	styleUrls: ['./add-quotation.component.less'],
	providers: [Customer, Quotation],
	encapsulation: ViewEncapsulation.None,
})
export class AddQuotationComponent extends AddOrderComponent<Quotation>
	implements OnInit {
	form: FormGroup = this.fb.group({
		customer: this.fb.group({
			id: [null],
			name: ['', Validators.required],
			telephone: [''],
			email: [''],
			tax_id: [''],
			addresses: this.fb.array([
				this.fb.group({
					address1: [null],
					city: [null],
					territory: [null],
					country: [null],
					zipcode: [null],
				}),
			]),
		}),
		project: this.fb.group({
			id: [null],
			codename: [null],
		}),
		lead_time: ['4 Weeks', Validators.required],
		currency: ['THB', Validators.required],
        discount: [0, [
            Validators.required,
            Validators.min(0)
        ]],
        second_discount: [0, [
            Validators.required,
            Validators.min(0)
        ]],
		vat: [7, [Validators.required, Validators.min(0)]],
		remarks: [''],
		items: this.fb.array([], ArrayValidators.minLength(1)),
	});

	protected _progress_check = {
		customer: false,
		project: false,
	};

	constructor(
		public quotation: Quotation,
		public customer: Customer,
		public customerService: CustomerService,
		public upholstery: Upholstery,
		public upholsteryService: UpholsteryService,
		public project: Project,
		public projectService: ProjectService,
		protected fb: FormBuilder,
		protected snackBar: MatSnackBar,
		protected dialogRef: MatDialogRef<AddQuotationComponent>
	) {
		super(
			quotation,
			customer,
			customerService,
			upholstery,
			upholsteryService,
			project,
			projectService,
			fb,
			snackBar,
			dialogRef
		);
		/*
        customer.addresses = [{
          address1: null,
          territory: null,
          city: null,
          country: null,
          zipcode: null
        }];
        */
		this.quotation.items = [];
		this.quotation.customer = customer;
		this.quotation.customer.addresses = [new Address()];
	}

	ngOnInit() {}

	public get customerControl(): AbstractControl {
		return this.form.get('customer');
	}

	public get orderControl(): FormGroup {
		return this.fb.group({
			lead_time: this.form.get('lead_time'),
			discount: this.form.get('discount'),
			second_discount: this.form.get('second_discount'),
			vat: this.form.get('vat'),
			remarks: this.form.get('remarks'),
		});
	}

	public get projectControl(): AbstractControl {
		return this.form.get('project');
	}

	public get subtotal(): number {
		let subtotal = 0;
		let item: AbstractControl;

		for (item of this.items.controls) {
			subtotal += this.getItemTotal(item);
		}

		return subtotal;
	}

	public get discount(): number {
		const subtotal = this.subtotal;
		return subtotal * (this.form.get('discount').value / 100);
	}

	public get total(): number {
		return this.subtotal - this.discount;
	}

	public get secondDiscount(): number {
        return this.total * (this.form.get('second_discount').value / 100);
	}

    public get vat(): number {
        return (this.total - this.secondDiscount) * (this.form.get('vat').value / 100);
    }

    public get grandTotal(): number {
        return this.total - this.secondDiscount + this.vat;
    }
    
	public getItemTotal(item: AbstractControl): number {
		const qtyCtrl = item.get('quantity');
		const priceCtrl = item.get('price');

		return (
			(qtyCtrl ? qtyCtrl.value : 0) * (priceCtrl ? priceCtrl.value : 0)
		);
	}

	public create(): void {
		if (this.form.valid) {
			this.form.disable();
			this.form.enable();

			const formVal = this.form.value;
			Object.assign(this.quotation, formVal);

			this.form.disable();

			// Recreate Customer
			const c = new Customer(this.customerService);
			Object.assign(c, formVal.customer);
			this.quotation.customer = c;

			if (this.quotation.customer instanceof Customer) {
				this.quotation.customer.save(() => {
					this._progress_check.customer = true;
					this._create();
				});
			}

			// Recreate Project
			if (formVal.project.codename && formVal.project.codename !== '') {
				const p = new Project(this.projectService);
				Object.assign(p, formVal.project);
				this.quotation.project = p;

				if (!this.quotation.project.id) {
					this.quotation.project.save(() => {
						this._progress_check.project = true;
						this._create();
					});
				} else {
					this._progress_check.project = true;
					this._create();
				}
			} else {
				delete formVal.project;
				delete this.quotation.project;
				this._progress_check.project = true;
			}
		}
	}

	protected _create(): void {
		if (this._progress_check.project && this._progress_check.customer) {
			this.form.disable();
			this.snackBar.open('Creating Quotation...', 'Dismiss');
			this.quotation.save((res: Quotation) => {
				this.snackBar.open('Quotation Created', 'Dismiss', {
					duration: 3000,
				});
				this.form.reset();
				this.dialogRef.close(res);
			});
		}
	}
}
