import { Component, OnInit, OnDestroy } from '@angular/core';
import { Observable ,  BehaviorSubject } from 'rxjs';
import { FormControl } from '@angular/forms';

import { Quotation } from '@app/core';
import {
	QuotationComponent,
	AddQuotationComponent,
} from '../components';
import { MatDialogRef, MatDialog } from '@angular/material';
import { debounceTime ,  distinctUntilChanged, take } from 'rxjs/operators';
import { DatePipe } from '@angular/common';


@Component({
  selector: 'app-quotations',
  templateUrl: './quotations.component.html',
  styleUrls: ['./quotations.component.less'],
  providers: [QuotationComponent, DatePipe]
})
export class QuotationsComponent implements OnInit, OnDestroy {
    private qList: BehaviorSubject<Quotation[]> = new BehaviorSubject([] as Quotation[]);
    quotations: Observable<Quotation[]> = this.qList.asObservable();
    myControl = new FormControl();
    searchTerm: BehaviorSubject<string> = new BehaviorSubject('');

    constructor(quotation: Quotation, public dialog: MatDialog) {
        // Initial population by request
        quotation.query().subscribe((results: Quotation[]) => this.qList.next(results));

        this.searchTerm.pipe(
            debounceTime(500),
            distinctUntilChanged()
        )
        .subscribe((x: string) => {
            quotation.query({q: x}).pipe(
                take(1)
            ).subscribe(
                (results: Quotation[]) => {
                    const currentList: Quotation[] = this.qList.getValue();
                    const keys: number[] = currentList.map((q: Quotation) => q.id) as number[];
                    const newList: Quotation[] = currentList.concat(
                        results.filter((v: Quotation) => !keys.includes(v.id as number))
                    );

                    this.qList.next(newList);
                }
            );
        });
    }

    ngOnInit() {
    }

    ngOnDestroy() {
        this.searchTerm.unsubscribe();
        this.qList.unsubscribe();
    }

    public showCreateQuotationModal() {
        const dialogRef: MatDialogRef<AddQuotationComponent> = this.dialog.open(AddQuotationComponent, {
            width: '90vw',
            height: '90vh',
            maxWidth: '60em',
            panelClass: 'athena-dialog-container'
        });

        dialogRef.beforeClose().pipe(take(1)).subscribe((result: Quotation) => {
            if (result) {
                if (result instanceof Quotation) {
                    const currentList: Quotation[] = this.qList.getValue();
                    const newList: Quotation[] = currentList.concat([result]);  

                    this.qList.next(newList);                 
                }
            }

        });
    }
}
